<template>
  <div class="student-view">
    <div class="header">
      <div class="left">
        <div class="img-box">
          <img :src="imgSrc" :draggable="false">
        </div>
        <div class="base-infor">
          <div class="name">{{name}}</div>
          <div class="detail">
            <div class="detail-line">
              <span class="content">
                <span class="label">学校：</span>
                <span class="text">{{school}}</span>
              </span>
              <span class="content">
                <span class="label">班级：</span>
                <span class="text">{{className}}</span>
              </span>
            </div>
            <div class="detail-line">
              <span class="content">
                <span class="label">年级：</span>
                <span class="text">{{grade}}</span>
              </span>
            </div>
          </div>
        </div>
      </div>
      <div class="right">
        <div class="title">上课提醒</div>
        <div class="detail">
          <div>课程：&nbsp;&nbsp;{{classStartName}}</div>
          <div>教室：&nbsp;&nbsp;{{classStartRoom}}</div>
          <div>时间：&nbsp;&nbsp;{{classStartTime}}</div>
        </div>
        <div class="bell-icon">
          <i class="iconfont icon-lingdang"></i>
        </div>
      </div>
    </div>
    <div class="message-part">
      <div class="message-task">
        <div class="message fl">
          <div class="mt-header">
            <div class="icon"></div>
            <el-badge :value="2">
              <div class="text">我的消息</div>
            </el-badge>
            <div class="more">更多</div>
          </div>
          <div class="box">
            <div class="mt-item" v-for="(item,index) in messageList" :key="index">
              <div class="head">
                <span class="iconfont" :class="{'icon-yidu':item.isRead,'icon-weidu':!item.isRead}"></span>
                <span class="text" :class="{'gray-text':item.isRead}">{{item.text}}</span>
              </div>
              <div class="bottom">
                <span class="fl">{{item.className}}</span>
                <span class="fr">{{item.time}}</span>
              </div>
            </div>
          </div>
        </div>
        <div class="task fr">
          <div class="mt-header">
            <div class="icon"></div>
            <el-badge :value="12">
              <div class="text">我的任务</div>
            </el-badge>
            <div class="more">更多</div>
          </div>
          <div class="box">
            <div class="mt-item" v-for="(item,index) in taskList" :key="index">
              <div class="head">
                <span class="iconfont" :class="item.icon"></span>
                <span class="text" :class="{'gray-text':item.isRead}">{{item.text}}</span>
              </div>
              <div class="bottom">
                <span class="fl">{{item.className}}</span>
                <span class="fr">{{item.time}}</span>
                <span class="clear"></span>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="trends">
        <div class="mt-header">
          <div class="icon"></div>
          <el-badge :value="12">
            <div class="text">最新动态</div>
          </el-badge>
          <div class="more">更多</div>
        </div>
        <div class="box">
          <el-input class="text-area" type="textarea" :rows="4" v-model="comment"></el-input>
          <el-button class="send-btn" type="primary" size="small">发表动态</el-button>
          <div class="talk-box">
            <div class="msg-item" v-for="(item,index) in talkList" :key="index">
              <div class="user-info">
                <div class="img-box">
                  <img :src="item.avater" :draggable="false">
                </div>
                <div class="detail">
                  <div class="name">{{item.name}}</div>
                  <span
                    class="tag"
                    :class="{'student-tag':item.tag=='学生','teacher-tag':item.tag=='教师'}"
                  >{{item.tag}}</span>
                </div>
              </div>
              <div class="content">{{item.content}}</div>
              <div class="desc">
                <span class="fl">{{item.time}}</span>
                <span class="good">
                  <i class="iconfont icon-dianzan_active"></i>
                  {{item.likes}}
                </span>
                <span class="re-back">回复</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="arrange-part">
      <div class="mt-header">
        <div class="icon"></div>
        <el-badge :value="12">
          <div class="text">课程安排</div>
        </el-badge>
        <div class="more">更多</div>
      </div>
      <div class="box">
        <div class="current-lesson-head">
          <div>{{currentLessonDate}}</div>
          <div class="text">第{{currentLessonText}}周（共{{currentLessonNum}}节课）</div>
        </div>
        <div class="lesson-item" v-for="(item,index) in lessonList" :key="index">
          <div class="title">{{item.title}}</div>
          <div class="detail">
            <span>{{item.place}}</span>
            <span>{{item.date}}</span>
            <span>{{item.range}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
