<style lang="less" scoped>
.student-view {
  width: 100%;
  .gray-text {
    color: #aaa;
  }
  .mt-header {
    width: 100%;
    height: 32px;
    text-align: left;
    line-height: 20px;
    border-bottom: 1px solid #aaa;
    .icon {
      width: 4px;
      height: 20px;
      background: #317fff;
      float: left;
    }
    .text {
      padding: 0 8px;
    }
    .more {
      float: right;
      color: #7eb6ff;
      font-size: 0.8rem;
      &:hover {
        cursor: pointer;
      }
    }
  }
  .box {
    width: 100%;
    .mt-item {
      width: 100%;
      height: 60px;
      margin-top: 12px;
      border-bottom: 1px dashed #aaa;
      text-align: left;
      &:hover {
        cursor: pointer;
      }
      .head {
        width: 100%;
        .iconfont {
          font-size: 1.5rem !important;
          vertical-align: middle;
          color: #317fff;
        }
        .icon-yidu {
          color: #7eb6ff !important;
        }
        .text {
          margin-left: 5px;
        }
      }
      .bottom {
        width: 100%;
        margin-top: 5px;
        color: #aaa;
        font-size: 0.8rem;
      }
    }
  }
  .header {
    width: 100%;
    height: 165px;
    background: #fff;
    padding: 22px;
    text-align: left;
    .left {
      width: 65%;
      height: 100%;
      float: left;
      border-right: 1px solid #aaa;
      .img-box {
        width: 115px;
        height: 100%;
        float: left;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .base-infor {
        width: 320px;
        height: 100%;
        float: left;
        margin-left: 36px;
        .name {
          font-size: 1.52rem;
          font-weight: 518;
          margin-top: 8px;
        }
        .detail {
          width: 100%;
          margin-top: 28px;
          font-size: 0.9rem;
          .content {
            width: 50%;
            margin-bottom: 12px;
            float: left;
            .label {
              color: #aaa;
            }
          }
        }
      }
    }
    .right {
      width: 28%;
      height: 100%;
      float: left;
      margin-left: 42px;
      position: relative;
      .title {
        font-size: 1.25rem;
        font-weight: 518;
      }
      .detail {
        width: 100%;
        margin-top: 18px;
        div {
          margin-top: 12px;
          font-size: 0.9rem;
          color: #aaa;
        }
      }
      .bell-icon {
        width: 36px;
        height: 36px;
        line-height: 36px;
        border-radius: 50%;
        background: #df2b2b;
        position: absolute;
        top: 0;
        right: 0;
        text-align: center;
        .iconfont {
          color: #fff;
          font-size: 1.36rem !important;
        }
      }
    }
  }
  .message-part {
    width: 66%;
    float: left;
    margin-top: 22px;
    .message-task {
      width: 100%;
      height: 310px;
      .message,
      .task {
        width: 48.5%;
        height: 100%;
        background: #fff;
        padding: 12px 22px;
      }
    }
    .trends {
      width: 100%;
      height: 700px;
      margin-top: 22px;
      background: #fff;
      padding: 12px 22px;
      .box {
        width: 100%;
        padding: 22px 0;
        .text-area {
          margin: 0 auto;
        }
        .send-btn {
          float: right;
          margin-top: 12px;
        }
        .talk-box {
          width: 100%;
          height: 400px;
          margin-top: 58px;
          .msg-item {
            width: 100%;
            height: 158px;
            border-top: 1px solid #aaa;
            padding: 12px 8px;
            .user-info {
              width: 100%;
              height: 52px;
              .img-box {
                width: 52px;
                height: 100%;
                float: left;
                border-radius: 50%;
                overflow: hidden;
                img {
                  width: 100%;
                  height: 100%;
                }
              }
              .detail {
                float: left;
                margin-left: 22px;
                text-align: left;
                .name {
