              height: 52px;
              float: right;
              text-align: left;
              margin-top: 8px;
            }
            .desc {
              width: 478px;
              float: right;
              color: #aaa;
              font-size: 0.9rem;
              user-select: none;
              .re-back,
              .good {
                float: right;
                margin-left: 12px;
                &:hover {
                  cursor: pointer;
                }
              }
              .good:hover {
                color: #63a9ff;
              }
            }
          }
        }
      }
    }
  }
  .arrange-part {
