      }
    }
    .lesson-item {
      width: 100%;
      height: 52px;
      border-bottom: 1px dashed #aaa;
      text-align: left;
      margin-top: 8px;
      .title {
        font-weight: 555;
      }
      .detail {
        width: 100%;
        color: #aaa;
        margin-top: 6px;
        font-size: 0.8rem;
        display: flex;
        justify-content: space-between;
      }
    }
  }
}
</style>



